import React, {useEffect, useState, useRef} from 'react';
import './App.css';
import Title from '../Title/Title';
import List from '../List/List';
import SearchBar from '../SearchBar/SearchBar'
import About from '../About/About'
import Details from '../Details/Details'
import { HashRouter as Router, Route } from 'react-router-dom'
import {MapContainer, TileLayer, CircleMarker, Marker, Popup, useMapEvents, useMap, SVGOverlay } from 'react-leaflet'
// import L from 'leaflet'
// import 'leaflet/dist/leaflet.css';

import data from '../wad-data';

// const customIcon = L.icon({
//   iconUrl: 'marker.png',
//   iconSize: [30, 30]
// });

// L.Marker.prototype.options.icon =customIcon;

function App() {
  const [myData, setMyData] = useState(data);
  const [sortType, setSortType] = useState('title');
  const [sortedData, setSortedData] = useState(data);
  const [genre, setGenre] = useState('Land Art');
  const [searchTerm, setSearchTerm] = useState('');
  const [searchResults, setSearchResults] = useState();
  const [selected, setSelected] = useState(0);
  const [markers, setMarkers] = useState([]);
 
  // const customMarker = L.icon({ iconUrl: require('./marker.png'), })
  const handleItemClick = (i) => {
    setSelected(i);
  }

  const MapEvent = ({selected,data}) => {
    const map = useMap()
    map.flyTo([data[selected].geo.lat, data[selected].geo.lng ], 7 , { duration: 3 } );
    return null
  }

  useEffect(() => {
    setMyData(data.filter(obj => obj.category === genre));
  }, [genre]);

// useEffect(() => {
//   if (myData === undefined || myData.length === 0 ) return;
//   setSortedData([...myData].sort((a,b)=>{return a.title.charAt(0).localeCompare(b.title.charAt(0))}))

// }, [myData]);

useEffect(() => {
  if (myData === undefined || myData.length === 0 ) return;
  if (sortType=='title') {
    setSortedData([...myData].sort((a,b)=>{return a.title.charAt(0).localeCompare(b.title.charAt(0), undefined, {numeric: true})}))
  } else if (sortType=='credit') {
    setSortedData([...myData].sort((a,b)=>{return a.credit.charAt(0).localeCompare(b.credit.charAt(0))}))
  } else if (sortType=='year') {
    setSortedData([...myData].sort((a,b)=>{return b.year.slice(0,4) - a.year.slice(0,4)}))
  }
}, [myData,sortType]); 

  useEffect(() => {
    setSelected(sortedData[0].id)

    for (let i = 0; i < sortedData.length; i++) {
      console.log(sortedData[i].id);
    }
      setMarkers(
        sortedData.map((obj) => ( 
          <CircleMarker center={[obj.geo.lat, obj.geo.lng]} pathOptions={{color: 'pink', fillOpacity: '.8'}} radius={10} >
           <Popup className="Popup">
              <h3>
                <a href={`#/details/${obj.id}`} onClick={()=>{setSelected(obj.id)}}>{obj.title}</a>
              </h3>
              {obj.id}<br />
              {obj.credit}<br />{obj.address}
            </Popup>
          </CircleMarker>
      ))
    ) 

  }, [sortedData]);

  useEffect(() => {
    if (searchTerm==='') return;
    setMyData(data.filter(obj => obj.category.toLocaleLowerCase().includes(searchTerm.toLocaleLowerCase())  
                               ||obj.keywords.toLocaleLowerCase().includes(searchTerm.toLocaleLowerCase())
                               ||obj.title.toLocaleLowerCase().includes(searchTerm.toLocaleLowerCase())
                               ||obj.credit.toLocaleLowerCase().includes(searchTerm.toLocaleLowerCase())
                               ||obj.desc.toLocaleLowerCase().includes(searchTerm.toLocaleLowerCase())
                               ||obj.address.toLocaleLowerCase().includes(searchTerm.toLocaleLowerCase())
    ))
  }, [searchTerm]);


  return (
    <Router basename="/gems_by_wad">
 
      <div className="App">
        <Title className="Title" selected={selected} genre={genre}/>
        <SearchBar className="SearchBar" searchTerm={searchTerm} setSearchTerm={setSearchTerm} searchResults={searchResults} setSearchResults={setSearchResults} genre={genre} setGenre={setGenre} />
        <Route className="List" exact path="/" render={(props) => <List {...props} onItemClick={handleItemClick} myData={sortedData} selected={selected} setMyData={setSortedData} sortType={sortType} setSortType={setSortType} />}/>
        {/* <Route className="List" exact path="/" render={(props) => <List {...props} onItemClick={handleItemClick} />}/> */}
        <Route className="Details" path="/details/:id" component={Details} data={myData} selected={selected}/>
        <Route className="About" path="/about" component={About} />
        <MapContainer className="Map" center={[39.50, -98.35]} zoom={7} data={sortedData} selected={selected}>
          <TileLayer url="https://api.mapbox.com/styles/v1/khnkjm/ckk1zhb1k2yxv18ldlvf6znch/tiles/256/{z}/{x}/{y}@2x?access_token=pk.eyJ1Ijoia2hua2ptIiwiYSI6ImNrajJsM2RsOTBoMWkyc3BrZWoxd3g5OXUifQ._RV3zklmvaVAWCaEx4JQsQ" />          
          {/* <TileLayer url="https://api.mapbox.com/styles/v1/khnkjm/ckjtnpxoa0ih419mxq0vqbjbv/tiles/256/{z}/{x}/{y}@2x?access_token=pk.eyJ1Ijoia2hua2ptIiwiYSI6ImNrajJsM2RsOTBoMWkyc3BrZWoxd3g5OXUifQ._RV3zklmvaVAWCaEx4JQsQ" attribution="&copy; <a href=&quot;https://www.openstreetmap.org/copyright&quot;>OpenStreetMap</a> contributors" /> */}
          <MapEvent data={data} selected={selected}/>
          {markers}
        </MapContainer>
      </div>
    </Router>
  );
}

export default App;
