import React, {useState,useEffect} from 'react';
import './SearchBar.css';
import { NavLink } from 'react-router-dom'

function SearchBar({searchTerm, setSearchTerm, genre, setGenre}) {
  const [activeLink, setActiveLink] = useState('Land Art');

  useEffect(() => {
    setActiveLink(genre);
  }, [genre]);
  
    return (
      <div className="SearchBar">
        <div>
          <NavLink className={activeLink === "Land Art"?"nav-link-active":"nav-link"}  exact to="/" onClick={()=>{setGenre('Land Art')}}>Land Art</NavLink>
          <NavLink className={activeLink === "Installation"?"nav-link-active":"nav-link"}   exact to="/" onClick={()=>{setGenre('Installation')}}>Installation</NavLink>
          <NavLink className={activeLink === "Dreamscape"?"nav-link-active":"nav-link"}  exact to="/" onClick={()=>{setGenre('Dreamscape')}}>Dreamscape</NavLink>
          <NavLink className="nav-link" activeClassName="nav-link-active" to="/about" onClick={()=>{setActiveLink('About')}}>About</NavLink>
          <form className="nav-link">
            <input 
              className="input"
              type="text"
              value={searchTerm}
              placeholder=""
              onChange={(e) => setSearchTerm(e.target.value)}
            />
            <button
              className="button"
              type='submit'
              onClick={(e) => setSearchTerm(e.target.value)}
              >Submit</button>
          </form>
        </div>
        <br />
      </div>
    )
  }
 
export default SearchBar