import React, { useEffect } from 'react';
import './Card.css'
import { Link } from 'react-router-dom'
import {MapContainer, TileLayer, Marker, Popup, useMap, useMapEvents, CircleMarker } from 'react-leaflet'



const Card = (props) => {

  const {id, title, credit, address, image, caption, handleClick } = props;
    
  useEffect(() => {
    }, []);

  return (  
      <div className='Card' id={id} onClick={(e)=> handleClick(id)} >
          <h2>
            <Link to={`/details/${id}`}>{title}</Link>
          </h2>
           <Link to={`/details/${id}`}>
           <div className="container">
              <img src={`${process.env.PUBLIC_URL}images/${image}`} width="300" height="300" alt="title" />
              {/* <div className="bottom-right">{caption}</div> */}
              {/* <div className="center">{id}</div> */}
              </div>
          </Link>
          <h3>{credit}</h3>
          <div>{address}</div>
      </div>
  );
}
 
export default Card;