import React from 'react';
import Card from '../Card/Card';
import './List.css';

const List = ({onItemClick,myData,selected, sortType,setSortType}) => {

  const cards = myData.map(({ id, title, credit, address, images, captions, geo}) => {

    return (
      
      <Card
        id={id}
        key={id}
        title={title}
        credit={credit}
        address={address}
        image={images[0]}
        caption={captions[0]}
        geo = {geo}
        handleClick={onItemClick}
      />
    )
  })

  return (
    <div className='List'>
      <select className='menu' onChange={e=>setSortType(e.target.value)}>
        <option value="title">Title</option>
        <option value="credit">Credit</option>
        <option value="year">Year</option>
    </select>
    {cards}
    </div>
  );
}
 
export default List;